body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow-y: scroll;
  /* height: auto */
}
*{
  padding:0;margin:0;
}
html,body,#root{
 height: 100%;
  width: 100%;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #ffffff;
  height: 64px;
  color: white;
  width: 100%;
  -ms-flex: 0 0;
      flex: 0 0
}
.App-main{
  width: 100%;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  height: auto;
}
.side{
  -ms-flex:1 1 300px;
      flex:1 1 300px;
  min-width: 0;
}
@media screen and (max-width: 700px) {
  .side {
      display: none
  }
}
.main{
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  min-width: 300px;
}
.App-footer{
  height: 100px;
  background:rgb(33,33,33);
  color: aliceblue;
  width: 100%;
  font-size: 12px;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  line-height: 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 20px
}
.App-footer a{
  cursor: pointer;
  color:#fff;
  padding: 10px;
  text-decoration: none;
}
.App-footer svg{
  position: relative;
  top:8px

}
.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



